import { axiosAjax } from 'multiPublic/index'
import {
    GETNEWSDETAILS,
    GETRECOMMENDNEWS,
    GETRELATEDNEWS,
    GETFEATUREDETAILS,
    GETHOTNEWSVIDEO,
    GETMOREFEATURE
} from '../constants/news'
import { ACTIONERROR } from 'multiRedux/constants/public'

/** ---------------------------------------- pure ajax ---------------------------------------- */

/**
 * @desc 
 * @Params {params} params = {newsId, passportId, token, status:1-1}
 */
export const favoriteNews = (params) => axiosAjax({
    type: 'get',
    url: '/info/news/collect',
    params: params
})

/**
 * @desc 
 * @Params {params} params = {idid, ifRecommend}
 */
export const statisticsNews = (params) => axiosAjax({
    type: 'get',
    url: '/info/news/addreadcount',
    params: params
})

/** ---------------------------------------- redux ajax ---------------------------------------- */

/**
 * @desc 
 * @Params {params} params = {tagstags, idid, newsCounts, publishTime}
 */
export const getRelatedNews = (params) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'get',
                url: '/info/news/relatednews',
                params: params
            })
            if (data.code === 1) {
                dispatch({
                    type: GETRELATEDNEWS,
                    data: data.obj
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

