/**
 * @desc 
 * @Params {params} params = {currentPage, pageSize, channelId}
 */
export const getNewsDetails = (params) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'get',
                url: '/info/news/getbyid',
                params: params
            })
            if (data.code === 1) {
                dispatch({
                    type: GETNEWSDETAILS,
                    data: data.obj
                })
            }

            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

/**
 * @desc 
 * @Params {params, loadType} params = {id, currentPage, pageSize}
 * loadType,'isMore'
 */
export const getFeatureDetails = (params, loadType) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'get',
                url: '/info/topic/info',
                params: params
            })
            if (data.code === 1) {
                if (loadType) {
                    dispatch({
                        type: GETMOREFEATURE,
                        data: data.obj
                    })
                } else {
                    dispatch({
                        type: GETFEATUREDETAILS,
                        data: data.obj
                    })
                }
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

// 
export const getHotNewsVideo = (id) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'get',
                url: '/info/news/hot/info',
                params: {
                    infoCounts: 6
                }
            })
            dispatch({
                type: GETHOTNEWSVIDEO,
                data: data.obj
            })
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}
