import { axiosAjax } from 'multiPublic/index'
import {
    GETNOTICELIST,
    GETBOURSELIST,
    GETBOURSESTATISTICSLIST,
    GETNOTICEDETAIL
} from '../constants/notice'
import { ACTIONERROR } from 'multiRedux/constants/public'

/**
 * @desc 
 * @Params {params} params = {
 *     exchangeSymbol 
 *     type 
 *     search 
 *     queryTime 
 *     pageSize 
 * }
 */
export const getNoticeList = (params) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'GET',
                url: '/info/notice/web/list',
                params: params
            })
            if (data.code === 1) {
                dispatch({
                    type: GETNOTICELIST,
                    data: data
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

/**
 * @desc 
 * @Params {params} params = {}
 */
export const getBourseList = () => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'GET',
                url: '/info/notice/resource'
            })
            if (data.code === 1) {
                data.obj.exchange.unshift({
                    symbol: '',
                    name: ''
                })
                data.obj.type.unshift({
                    id: '',
                    name: ''
                })
                dispatch({
                    type: GETBOURSELIST,
                    data: data
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

