import { axiosAjax } from '../../public'
import { ACTIONERROR } from '../constants/public'
import {
    FLASHUPDOWN,
    GETFLASHLIST,
    GETMOREFLASH
} from '../constants/flash'

/**
 * @desc 
 * @Params {params} params = {id, passportid, token, status 0:; 1:}
 */
export const getFlashList = (params, loadType) => {
    return async dispatch => {
        try {
            const data = await axiosAjax({
                type: 'get',
                url: '/info/lives/showlives',
                params: params
            })
            if (data.code === 1) {
                if (loadType === 'more') {
                    dispatch({
                        type: GETMOREFLASH,
                        data: data.obj
                    })
                } else {
                    dispatch({
                        type: GETFLASHLIST,
                        data: data.obj
                    })
                }
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

/**
 * @desc 
 * @Params {params} params = {id: id, status: 01, passportid, token}
 */
export const flashUpDownAjax = (params) => axiosAjax({
    type: 'post',
    url: '/info/lives/upordown',
    params: params,
    noLoading: true
})
export const getUporDown = (params) => {
    return async dispatch => {
        try {
            const data = await flashUpDownAjax(params)
            if (data.code === 1) {
                dispatch({
                    type: FLASHUPDOWN,
                    data: data.obj,
                    params: params
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}
