/**
 * @desc 
 * data  ArrayBufferViewBlobDOMString  Formdata
 *  request header  CORS-safelisted-request-header Content-Type 
 * application/x-www-form-urlencoded
 * multipart/form-data
 * text/plain
 *  DOMString , Blob  Formdata 
 */
// const syncReport = (url, { data = {}, headers = {} } = {}) => {
//     const xhr = new XMLHttpRequest()
//     xhr.open('POST', url, false)
//     xhr.withCredentials = true
//     Object.keys(headers).forEach((key) => {
//         xhr.setRequestHeader(key, headers[key])
//     })
//     xhr.send(JSON.stringify(data))
// }
const reportData = function (url, data, type) {
    if (window.navigator && window.navigator.sendBeacon) {
        let params = data

        if (type === 'formdata') {
            params = new FormData()
            Object.keys(data).forEach((key) => {
                let value = data[key]
                if (typeof value !== 'string') {
                    // formDataappend string  Blob
                    value = JSON.stringify(value)
                }
                params.append(key, value)
            })
        }

        if (type === 'blob') {
            params = new Blob([JSON.stringify(data), {
                type: 'application/x-www-form-urlencoded'
            }])
        }

        if (!type) {
            params = JSON.stringify(params)
        }

        navigator.sendBeacon(url, params)
    } else {
        // xhr
        const xhr = new XMLHttpRequest()
        xhr.open('POST', arguments[0], true)
        xhr.send(arguments[1])
    }
}
export const gather = (payload) => {
    const params = {
        ip: '',
        type: 'stat',
        payload
    }
    const url = `${apiHost}/passport/account/recommend/gather`

    reportData(url, params)
}

